.navbar {
  background: linear-gradient(90deg, rgb(255, 255, 255) 0%, rgb(255, 255, 255) 100%);
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
  top: 0;
  z-index: 999;
  border:0.2px solid rgb(238, 238, 238);
  opacity:0.95;
}

.navbar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50px;
  max-width: 1500px;
}

.navbar-logo {
  color: rgb(255, 255, 255);
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 1.5rem;
  display: flex;
  align-items: center;
}

.img-logo{
  list-style: none;
  justify-content: end;
  margin-left: auto;
}

.fa-typo3 {
  margin-left: 0.5rem;
  font-size: 1.8rem;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-gap: 15px;
  list-style: none;
  text-align: center;
  width:85vw;
  justify-content: end;
  margin-right: auto;
}

.nav-item {
  height: 80px;
}

.nav-links {
  color: rgb(110, 110, 110);
  font-size: 15px;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
}

.nav-links:hover {
  border-bottom: 4px solid rgb(0, 64, 116);
  transition: all 0.2s ease-out;
}



/* =============== DEFAULT HTML - LIST STYLES =============== */
.navbar ul, ul li,
ol, ol li,
p, form, input {
	margin: 0;
	padding: 0;
}
.navbar p {
	margin-bottom: 10px;
}
.navbar ul, ul li {
	list-style: none;
}

.navbar a {
	text-decoration: none;
	outline: none;
	color: rgb(105, 105, 105);
	-webkit-transition: all .3s ease-in-out;
	   -moz-transition: all .3s ease-in-out;
		 -o-transition: all .3s ease-in-out;
		-ms-transition: all .3s ease-in-out;
			transition: all .3s ease-in-out;
}
.navbar a:hover {
	color: rgb(0, 64, 116);
	text-decoration: none;
}


/* =============== DEFAULT HTML - LINK STYLES =============== */


/*********/

.fa-bars {
  color: rgb(0, 64, 116);
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #1d1d1d;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    background-color: #fff;
    color: #242424;
    border-radius: 0;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: rgb(0, 64, 116);
    font-size: 2rem;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 1.5rem;
    background-color: transparent;
    color: #fff;
    padding: 14px 20px;
    border: 1px solid #fff;
    transition: all 0.3s ease-out;
  }

  .nav-links-mobile:hover {
    background: #fff;
    color: #242424;
    transition: 250ms;
  }
}


